body {
  background: yellow;
  display: flex;
  justify-content: center;
}
body #image-wrapper {
  display: flex;
}
body #image-wrapper div {
  margin: 0 20px;
}
body #image-wrapper #rock {
  background: url(/img/rock.png);
  height: 200px;
  width: 210px;
  cursor: pointer;
}
body #image-wrapper #paper {
  background: url(/img/paper.png);
  height: 200px;
  width: 250px;
  cursor: pointer;
}
body #image-wrapper #scissors {
  background: url(/img/scissors.png);
  height: 200px;
  width: 235px;
  cursor: pointer;
}